function fieldFactory(name, type, placeholder = 'example', value = '') {
  return { id: uniqid(), name, type, placeholder, value };
}

function fieldSetFactory(fieldsArr, removeable = false) {
  return {
    id: uniqid(),
    fields: fieldsArr,
    removeable,
  };
}

function sectionFactory(name, fieldsArr, expandable = true) {
  /**
   * This exists so each new field set will be served with fields which have unique id's
   * @returns Array of field objects
   */
  const create = () => {
    return fieldsArr.map((field) => {
      return fieldFactory(
        field.name,
        field.type,
        field.placeholder,
        field.value
      );
    });
  };
  const fieldsets = [];
  fieldsets.push(fieldSetFactory(create())); // Initial fields

  const appendFields = () => {
    fieldsets.push(fieldSetFactory(create(), true)); // add new field set that are removable
  };

  return {
    name,
    fieldsets,
    expandable,
    appendFields,
  };
}
