import React from 'react';
import './PreviewSection.css';
/**
 * Mediator: assigns and returns the appropriate Component for the section
 */
export default class PreviewSection extends React.Component {
  render() {
    const { section } = this.props;
    switch (section.name) {
      case 'General Info':
        return <PreviewGeneralInfo section={section} />;
      case 'Education':
        return <PreviewEducation section={section} />;
      case 'Job Experience':
        return <PreviewJobExp section={section} />;
      case 'Skills and Competencies':
        return <PreviewSkills section={section} />;
      case 'Languages':
        return <PreviewLanguages section={section} />;
    }
    return <div>{section.name}</div>;
  }
}

class PreviewSectionTitle extends React.Component {
  render() {
    const { title } = this.props;
    return (
      <div className="title">
        <h2>{title}</h2>
      </div>
    );
  }
}

