class PreviewGeneralInfo extends React.Component {
  render() {
    const { section } = this.props;
    const { fieldsets } = section;
    const [fname, lname, title, address, email, phone, description] =
      fieldsets[0].fields;
    return (
      <div className="preview-section general">
        <div className="name">
          <h1>{`${fname.value} ${lname.value}`}</h1>
        </div>
        <div className="title">
          <span>{title.value}</span>
        </div>
        <div className="address">
          <span>{address.value}</span>
        </div>
        <div className="email">
          <span>{email.value}</span>
        </div>
        <div className="phone">
          <span>{phone.value}</span>
        </div>
        <div className="description">
          <p>{description.value}</p>
        </div>
      </div>
    );
  }
}

//     'Education',
//       { name: 'Name of Institution',
//         type: 'text',
//         placeholder: 'Institute of Techno', },
//       { name: 'Major/Area of Study',
//         type: 'text',
//         placeholder: 'Computer Science', },
//       { name: 'From', type: 'text', placeholder: 'March 2015' },
//       { name: 'To', type: 'text', placeholder: 'March 2019' },
class PreviewEducation extends React.Component {
  render() {
    const { section } = this.props;
    const { fieldsets } = section;
    return (
      <div className="preview-section education">
        <PreviewSectionTitle title={section.name} />
        <div>
          {fieldsets.map((fieldset) => {
            const [school, study, from, to] = fieldset.fields;
            return (
              <div className="school" key={fieldset.id}>
                <div className="dates">
                  <span>
                    {from.value} - {to.value}
                  </span>
                </div>
                <div className="school-name">
                  <h3>{school.value}</h3>
                </div>
                <div className="major">
                  <span>{study.value}</span>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

//     'Job Experience',
//       fieldFactory('Company Name', 'text', 'McDonalds'),
//       fieldFactory('Position/Title', 'text', 'Burger Flipper'),
//       fieldFactory('Job Description', 'text', 'Flip Burgers'),
//       fieldFactory('From', 'text', 'March 2019'),
//       fieldFactory('To', 'text', 'Current'),
class PreviewJobExp extends React.Component {
  render() {
    const { section } = this.props;
    const { fieldsets } = section;
    return (
      <div className="preview-section job-exp">
        <PreviewSectionTitle title={section.name} />
        <div className="jobs">
          {fieldsets.map((fieldset) => {
            const [name, position, desc, from, to] = fieldset.fields;
            return (
              <div className="job">
                <div className="name">
                  <h3>{name.value}</h3>
                </div>
                <div className="position">
                  <span>{position.value}</span>
                </div>
                <div className="desc">
                  <p>{desc.value}</p>
                </div>
                <div className="date">
                  <span>
                    {from.value} - {to.value}
                  </span>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

