class PreviewSkills extends React.Component {
  render() {
    const { section } = this.props;
    const { fieldsets } = section;
    return (
      <div className="preview-section skills-and-competencies">
        <PreviewSectionTitle title={section.name} />
        <div className="skills">
          {fieldsets.map((fieldset) => {
            const [skill] = fieldset.fields;
            return <div>{skill.value}</div>;
          })}
        </div>
      </div>
    );
  }
}

