import React from 'react';
import SectionField from './SectionField';
import './SectionFieldSet.css';

export default class SectionFieldSet extends React.Component {
  constructor(props) {
    super(props);
    this.fieldset = this.props.fieldset;
    this.mouseEnterHandler = this.mouseEnterHandler.bind(this);
    this.mouseLeaveHandler = this.mouseLeaveHandler.bind(this);
    this.state = {
      fieldSetClasses: ['field-set'],
    };
    this.deleteBtn = (
      <button
        type="button"
        onClick={() => {
          this.props.handleFieldSetRemoval(this.fieldset);
        }}
        onMouseEnter={this.mouseEnterHandler}
        onMouseLeave={this.mouseLeaveHandler}
      >
        Remove
      </button>
    );
  }

  mouseEnterHandler() {
    this.setState({
      fieldSetClasses: this.state.fieldSetClasses.concat('highlight'),
    });
  }
  mouseLeaveHandler() {
    this.setState({
      fieldSetClasses: this.state.fieldSetClasses.slice(0, 1),
    });
  }
  render() {
    const { handleFieldInput } = this.props;
    return (
      <div className={this.state.fieldSetClasses.join(' ')}>
        <ul className="fields">
          {this.fieldset.fields.map((field) => (
            <SectionField
              field={field}
              key={field.name}
              handleFieldInput={handleFieldInput}
            />
          ))}
        </ul>
        {(() => {
          if (this.fieldset.removeable) {
            return this.deleteBtn;
          }
        })()}
      </div>
    );
  }
}
