import React from 'react';
import SectionName from './SectionName';
import SectionFieldSet from './SectionFieldSet';
import './Section.css';

export default class Section extends React.Component {
  render() {
    const {
      section,
      handleFieldAppending,
      handleFieldSetRemoval,
      handleFieldInput,
    } = this.props;
    return (
      <form className="section">
        <SectionName name={section.name} />
        {section.fieldsets.map((fieldset) => {
          return (
            <SectionFieldSet
              fieldset={fieldset}
              handleFieldSetRemoval={handleFieldSetRemoval}
              handleFieldInput={handleFieldInput}
            />
          );
        })}

        {(() => {
          if (section.expandable) {
            return (
              <button
                className="add"
                type="button"
                onClick={() => {
                  handleFieldAppending(section);
                }}
              >
                Add
              </button>
            );
          }
        })()}
      </form>
    );
  }
}
